<template>
    <form v-on:submit="sendQuery()" v-on:focus="focused()">
        <input type="search" name="" id="search_input" ref="search_input" value="Search..."
            v-on:focus="clearBox()"
        >
        <div class="search_card search" v-on:click="sendQuery()">
            <h4>Search</h4>          
        </div> 
    </form>   
</template>

<script>
export default {
    name:'SearchBar',
    methods:{
        clearBox: function(){
            this.$refs.search_input.value = "";
        },
        showDefaultText: function(){
             this.$refs.search_input.value = "Search...";
        },
        sendQuery(){
            const search_query = this.$refs.search_input.value;
            // console.log(s_bar);
            this.$router.push({path: "results", query:{q:search_query}});
        },
        focused(){
            console.log("search bar has focus somewhat");
        }
    }
}
</script>

<style scoped>
    input{
        flex-grow: 1;
        max-width: 95vw;
        margin: 5vh auto 5vh auto;
        padding: 5px 0px 5px 5px;
        font-size: 2em;
        color: rgb(200,200,200);
        border-radius: 5px;
        padding: 5px 0px 5px 5px;
    }

    input:focus{
        font-size: 2em;
        color: rgb(20,20,20);
    }

    .search{
        background-image: url('../assets/svg_assets/search-24px.svg');
        background-repeat: no-repeat;
        background-position: center;
        background-size: 30vw 10vh;
        background-color:rgb(29, 185, 185);
    }

    .search_card{
        height: 150px;
        width: 150px;
        margin: 2vw;
        border: 1px solid #3bd230;
        border-radius: 5px;
        background-color:rgb(244, 246, 246);
        float: left;
        align-self: center;
        border-radius: 50%;
        animation: shake 2.0s;
        animation-iteration-count: infinite;
        background-color: #3bd230;
        box-shadow: 0px 6px 6px 3px rgba(0,0,0,0.4);
    }

    .search_card >a {
        text-decoration: none;
     }

     .search_card >a >div{
         height: 100%;
         width: 100%;
     }

     .search_card >a >div >img{
        height: 40%;
        width: auto;
        margin-bottom: auto;
        align-self: center;
     }

     div.search_card:active{
         background-color: rgb(24, 206, 206);;
     }

     div.search_card:hover{         
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
     }

     @keyframes shake {
        0% { transform: translate(1px, 1px) rotate(0deg); }
        10% { transform: translate(-1px, -2px) rotate(-1deg); }
        20% { transform: translate(-2px, 0px) rotate(1deg); }
        30% { transform: translate(2px, 2px) rotate(0deg); }
        40% { transform: translate(1px, -1px) rotate(1deg); }
        50% { transform: translate(-1px, 2px) rotate(-1deg); }
        60% { transform: translate(-2px, 1px) rotate(0deg); }
        70% { transform: translate(2px, 1px) rotate(-1deg); }
        80% { transform: translate(-1px, -1px) rotate(1deg); }
        90% { transform: translate(1px, 2px) rotate(0deg); }
        100% { transform: translate(1px, -2px) rotate(-1deg); }
    }

     @media screen  and (min-width: 600px){
        .search_card{
            height: 25vh;
            width: 27vw;
            margin: 2vw;
        }

     }

      @media screen  and (max-height: 600px){
        .search_card{
            height: 25vw;
            width: 25vw;
            margin: 2vw;
            margin-left: 12vw;
            margin-right: 12vw;
            font-size: 0.75em;
            display: flex;
        }
        .search_card.search >h4{
                font-size: 1em;
                margin-bottom: 5px;
                align-self: flex-end ;
                margin-right: auto;
                margin-left: auto;
            }
     }

    @media screen  and (max-height: 360px){
            .search_card{
                height: 22vw;
                width: 22vw;
                margin: 2vw;
                margin-left: 12vw;
                margin-right: 12vw;
                display: flex;
                /* font-size: 0.7em; */
            }

            .search_card.search >h4{
                font-size: 1em;
                margin-bottom: 5px;
                align-self: flex-end ;
                margin-right: auto;
                margin-left: auto;
            }
        }

</style>